.Tile {
    display: block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;

    /* Critical */
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    -webkit-transition: all 0.1s ease-out;
    -o-transition: all 0.1s ease-out;
    transition: all 0.1s ease-out;
}

.Tile:before {
    content: '';
    float: left;
    padding-top: 100%;
}

.Tile-content {
    overflow: hidden;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
}

.Tile .Tile-source {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100vw;
}



