* {
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans,
        Helvetica Neue, sans-serifbody, html;
}

img {
    border-style: none;
}

body {
    overflow-y: hidden;
}

.Scene {
    position: relative;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0 auto;

    overflow: hidden;
}

.Scene:before {
    content: '';
    float: left;
    padding-top: 100%;
}

.Scene-content {
    position: relative;
}

.Winning-screen {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transition: all 0.8s ease-out;
    -o-transition: all 0.8s ease-out;
    transition: all 0.8s ease-out;
    opacity: 0;
}

.Winning-screen .Full-image {
    width: 100%;
    position: relative;
    display: block;
}

.Winning-screen .Origin-link {
    position: relative;
    text-decoration: none;
    margin: 5px;
    -ms-flex-item-align: end;
        align-self: flex-end;
    text-transform: uppercase;
    font-size: 10px;
    font-weight: 500;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    opacity: 0.45;
    color: black;
}

.Winning-screen .Origin-link:hover {
    opacity: 1;
}

.Winning-screen .Origin-link:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    bottom: -5px;
    left: 0;
    background-color: black;
    visibility: hidden;
    -webkit-transform: scaleX(0);
        -ms-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
}

.Winning-screen .Origin-link:hover:before {
    visibility: visible;
    -webkit-transform: scaleX(1);
        -ms-transform: scaleX(1);
            transform: scaleX(1);
}

.Winning-screen .New-game-button {
    color: #fff;
    background: #000;
    display: inline-block;
    width: 200px;
    height: 50px;
    border: 2px solid #000;
    font-size: 12px;
    text-transform: uppercase;
    cursor: pointer;
    text-align: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: relative;
    overflow: hidden;
    margin: 10px;
}



